@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-header {
  height: 20rem;
  background-color: var(--strong-cyan);
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.main-header h1 {
  color: var(--white);
  cursor: default;
  font-weight: 300;
  font-size: 5rem;
  margin: 0;
  padding: 0;
}


nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 1rem;
}

ul {
  list-style: none;
  display: -webkit-flex;
  display: flex;
}

.form-section {
  display: none;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.display-grid {
  display: grid;
}

.react-logo {
  height: 100%;
  width: auto;
  -webkit-animation: rotate 5s infinite;
          animation: rotate 5s infinite;
  color: var(--strong-dark-cyan);
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width: 650px) {
  .react-logo {
    display: none;
  }

  .form-section {
    grid-template-columns: 1fr;
  }
}


li {
  margin: 0 2em;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: auto auto;
  grid-gap: .5em;
}

li:hover {
  cursor: pointer;
}

.form-row {
  display: grid;
  grid-template-columns: 4em auto;
  margin: .75em 0em;
}

.form-row > * {
  font-family: 'Noto Sans JP', sans-serif;
}


form {
  width: 90%;
  padding: 2em 1em;
}

.btn-submit {
  background-color: var(--white);
  cursor: pointer;
}

.btn-submit:hover {
  background-color: var(--strong-cyan);
}


input {
  border: none;
  border-radius: none;
  padding: .5em 1em;
  margin: 0;
  width: 100%;
}

input:focus {
  outline: none;
  outline: .15em solid var(--strong-cyan);
}


select {
  border: none;
  margin: 0;
  width: 100%;
  padding: .5em 1em;
}

select:focus {
  outline: 0.15em solid var(--strong-cyan);
}

select:focus-within {
  background-color: var(--white);
}

.character-card {
  border-radius: 1.25em;
  background-color: var(--strong-cyan);
  display: grid;
  grid-template-columns: 40% auto;
  grid-gap: 1em;
}

img {
  height: 100%;
  width: 100%;
  border-radius: 1.25em 0 0 1.25em;
}

.character-card__info {
  text-align: left;
}

.character__name {
  font-size: 2rem;
  font-weight: bold;
  color: var(--white);
  margin-top: 0.5em;
}

.character__status,
.character__gender {
  font-size: 1.15rem;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  margin: 3rem;
}

.character-cards {
  box-sizing: content-box;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-gap: 2%;
  margin-bottom: 3rem;
}

@media screen and (max-width: 770px) {
  .character-cards {
    grid-template-columns: auto;
  }
}

.character-result {
  display: grid;
  grid-template-columns: 3rem auto;
  height: 3rem;
  -webkit-align-items: center;
          align-items: center;
}

.character-result__img {
  height: auto;
  width: 100%;
}

.character-name {
  background-color: var(--strong-cyan);
  color: #3e2e3d;
  margin: 0;
  padding: 0;
  height: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: .75em;
  border-radius: 0 2em 2em 0;
}


.pages-navigator {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: auto;
}

.page-indicator-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.page-indicator {
  --margin: 1rem;
  margin: 0 var(--margin) 0 var(--margin);
  color: black;
}

.actual-page {
  font-weight: 700;
  color: var(--strong-dark-cyan);
}

.page-nav-arrow {
  cursor: pointer;
}

.page-nav-arrow.disabled {
  color: #454545;
  cursor: not-allowed;
}


.search-results {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  margin: 2em 1rem 2em 1rem;
}

.container-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 1rem 0 1rem;
}

@media screen and (max-width: 770px) {
  .search-results {
    grid-template-columns: auto;
  }
}

:root {
  --strong-cyan: #4cdbc4;
  --strong-dark-cyan: #17574d;
  --light-cyan: #c8eee6;
  --white: #f3fbfa;
}

body {
  font-size: 16px;
  font-family: "Noto Sans JP", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #c8eee6;
  background-color: var(--light-cyan);
  box-sizing: border-box;
}

input,
select {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}


